<template>
  <div class="profile">
    <header id="wx-header">
      <div class="center">
        <router-link to="/contact" tag="div" class="iconfont icon-return-arrow">
          <span>通讯录</span>
        </router-link>
        <span>详细资料</span>
      </div>
    </header>
    <div class="weui-cells">
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <img
            :src="'/api/file/showImg?fileUrl='+userInfo.user.headerUrl"
            alt
            class="self-header"
            style="width:60px"
          />
        </div>
        <div class="weui-cell__bd">
          <h4 class="self-nickname">
            {{userInfo.friendRemark||userInfo.user.remark||userInfo.user.nickname}}
            <span
              class="gender"
              :class="[userInfo.user.sex===1?'gender-male':'gender-female']"
            ></span>
          </h4>
          <p class="self-wxid" style="font-size: 13px;color: #999;">工号: {{userInfo.user.userId}}</p>
          <p
            class="nickname"
            style="font-size: 13px;color: #999;"
          >昵称:{{userInfo.user.nickname||'无'}}</p>
        </div>
      </div>
    </div>
    <div class="weui-cells">
      <div class="weui-cell weui-cell_access">
        <div class="weui-cell__bd">
          <p @click="setRemark">设置备注和标签</p>
        </div>
        <div class="weui-cell__ft"></div>
      </div>
    </div>
    <div class="weui-cells">
      <div class="weui-cell">
        <div class="weui-cell__bd">
          <p>地区</p>
        </div>
        <div class="weui-cell__ft" style="-webkit-flex: 4;text-align: left;">
          <span v-for="item in userInfo.user.area">{{item}}&nbsp;&nbsp;&nbsp;</span>
        </div>
      </div>
      <div class="weui-cell weui-cell_access">
        <div class="weui-cell__bd">
          <p>个人相册</p>
        </div>
        <div class="weui-cell__ft" style="-webkit-flex: 4;text-align: left;">
          <div class="album-list">
            <img
              :src="item.imgSrc"
              style="width:50px;margin:0 5px"
              v-for="item in userInfo.user.album"
            />
          </div>
        </div>
      </div>
      <div class="weui-cell weui-cell_access">
        <div class="weui-cell__bd">
          <p>更多</p>
        </div>
        <div class="weui-cell__ft"></div>
      </div>
    </div>

    <a
      href="javascript:;"
      class="weui-btn weui-btn_primary"
      style="width:90%;margin-top:20px;"
      @click="sendMsg"
    >发消息</a>
    <a href="javascript:;" class="weui-btn weui-btn_default" style="width:90%">视频</a>
  </div>
</template>
<script>
import qs from "qs";
export default {
  data() {
    return {
      pageName: ""
      // userInfo: ""
    };
  },
  computed: {
    userInfo() {
      let contacts = this.$store.state.allContacts;
      if (!this.$route.query.userId) {
        return;
      } else {
        //根据传过来的userId去匹配保存的用户
        for (var index in contacts) {
          if (contacts[index].user.userId === this.$route.query.userId) {
            return contacts[index];
          }
        }
      }
    }
  },
  methods: {
    //设置备注
    setRemark() {
      this.$router.push({
        path: "/contact/details/set-remark",
        query: {
          userId: this.$route.query.userId,
          remark: this.userInfo.friendRemark || this.userInfo.user.remark
        }
      });
    },
    sendMsg() {
      let params = {
        toUserId: this.userInfo.user.userId,
        roomType: "friend"
      };
      this.$http
        .post("/api/chat/creatChatRoom", qs.stringify(params))
        .then(res => {
          // console.log(res.data.data.roomId);
          if (res.data.code == 200) {
            this.$router.push({
              path: "/wechat/dialogue",
              query: {
                roomId: res.data.data.roomId,
                name: this.userInfo.user.nickname,
                userId: this.userInfo.user.userId,
                roomType: "friend"
              }
            });
          }
        });
    }
  },
  mounted() {
    console.log(this.userInfo);
    //根据userId查询用户信息
  }
};
</script>
<style>
</style>


